import React, { Component } from "react";

class About extends Component {
  render() {
    if (this.props.data) {
      var name = this.props.data.name;
      var profilepic = "images/" + this.props.data.image;
      var primary_bio = this.props.data.primary_bio;
      var secondary_bio = this.props.data.secondary_bio;
      var street = this.props.data.address.street;
      var city = this.props.data.address.city;
      var state = this.props.data.address.state;
      var zip = this.props.data.address.zip;
      var phone = this.props.data.phone;
      var email = this.props.data.email;
      var emailTwo = this.props.data.emailTwo;
      var emailThree = this.props.data.emailThree;
      var resumeDownload = this.props.data.resumedownload;
    }

    return (
      <section id="about">
        <div className="row">
          <div className="three columns">
            <img className="profile-pic" src={profilepic} alt="prof-pic" />
          </div>
          <div className="nine columns main-col">
            <h2>About Me</h2>
            <p style={{fontSize:22, fontWeight:'100'}}>{primary_bio}</p>
            <p>{secondary_bio}</p>
            <div className="row">
              <div className="columns contact-details">
                <h2>Contact Details</h2>
                <p className="address">
                  <span>{name}</span>
                  <br />
                  <span>
                    {street}
                    <br />
                    {city} {zip}, {state}
                  </span>
                  <br />
                  <span>{phone}</span>
                  <br />
                  <br />
                  <span>{`Official : ${email}`}</span>
                  <br />
                  <span>
                    {`University : ${emailTwo}`}
                    <span>
                      <br />
                      {`⠀⠀⠀⠀⠀⠀⠀ : ${emailThree}`}
                    </span>
                  </span>
                </p>
              </div>
              <div className="columns download">
                <p>
                  <a href={resumeDownload} className="button">
                    <i className="fa fa-download"></i>Download Resume
                  </a>
                </p>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default About;
